import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';

import { CommonModule } from '@angular/common';

import { CarteraVencidaComponent } from './cartera-vencida/cartera-vencida.component';
import { CobranzaComponent } from './cobranza/cobranza.component';
import { IngresosComponent } from './ingresos/ingresos.component';
import { AdeudosComponent } from './adeudos/adeudos.component';
import { NotificacionesComponent } from './notificaciones/notificaciones.component';

import { PruebajvcComponent } from './prueba-jvc/pruebajvc.component';

const routes: Routes = [
  {
    path: '',
    children: [
      { path: 'carteraVencida', component: CarteraVencidaComponent },
      { path: 'notificaciones', component: NotificacionesComponent },
      { path: 'cobranza', component: CobranzaComponent },
      { path: 'ingresos', component: IngresosComponent },
      { path: 'adeudos', component: AdeudosComponent },
      { path: 'pruebajvc', component: PruebajvcComponent },
    ],
  },
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule],
})
export class CobranzaRoutingModule {}
