import { Component, OnInit } from '@angular/core';

import * as _ from 'lodash';
// SERVICES
import { ApiRestCobranzaService } from '../services/api-rest-cobranza.service';
import { ApiRestSharedService } from '../../shared/services/api-rest-shared.service';

@Component({
  selector: 'app-cartera-vencida',
  templateUrl: './cartera-vencida.component.html',
  styles: [],
})
export class CarteraVencidaComponent implements OnInit {
  // CATALOGOS
  colors: string[] = [
    '#7E72A8',
    '#AE9AF5',
    '#D1F58D',
    '#A85177',
    '#F582B3',
    '#49BDFD',
    '#42D4E3',
    '#55FADC',
    '#42E398',
    '#49FD76',
  ];
  indexCategories = {
    0: 'CAT 0',
    CAT1: 'Categoría 1',
    CAT2: 'Categoría 2',
    CAT3: 'Categoría 3',
  };
  cat_categoriasAdeudo: any[];
  cat_anios: string[] = ['2020', '2019', '2018', '2017', '2016', '2015'];
  // NGMODEL
  chart_1_anio: string = '2019';
  chart_1_cat: any;

  chart_2_anio: string = '2019';
  chart_2_cat: any;
  //  DATA
  chart_1: any[];
  chart_2: any[];
  chart_2b: any;
  colorsChart_1: string[] = [
    '#49BDFD',
    '#42D4E3',
    '#55FADC',
    '#42E398',
    '#49FD76',
  ];
  colorsChart_2: string[] = [
    '#165BF6',
    '#1381D4',
    '#21C8EB',
    '#13D4C0',
    '#16F69F',
  ];

  constructor(
    private apiRestCobranzaService: ApiRestCobranzaService,
    private apiRestSharedService: ApiRestSharedService
  ) {
    this.getCatalogoCategoriasAdeudo();
  }

  ngOnInit(): void {
    this.getMontosCategoriaAnio();
    this.getMontosCategoriaMes();
  }

  chart_1_selectAnio(eve) {
    console.log('::::: CHART_1_ANIO: ', eve);
    if (!eve) this.chart_1_anio = '2019';
    this.getMontosCategoriaMes();
  }
  chart_1_selectCat(eve) {
    console.log('::::: CHART_1_CAT: ', JSON.stringify(eve));
    this.getMontosCategoriaMes();
  }

  chart_2_selectAnio(eve) {
    console.log('::::: CHART_2_ANIO: ', eve);
    if (!eve) this.chart_2_anio = '2019';
    this.getMontosCategoriaAnio();
  }
  chart_2_selectCat(eve) {
    console.log('::::: CHART_2_CAT: ', JSON.stringify(eve));
    this.getMontosCategoriaAnio();
  }

  /** ########## SERVICES ########## */

  getCatalogoCategoriasAdeudo() {
    this.apiRestSharedService.getCatalogo('CAT_ADEUDO').subscribe(
      (success) => {
        if (!!success) this.cat_categoriasAdeudo = success.body;
      },
      (error) => {}
    );
  }

  getMontosCategoriaMes() {
    const dataRequest = {
      anio: this.chart_1_anio,
      catAdeudo: this.chart_1_cat ? this.chart_1_cat.claveSIM : '',
    };
    this.apiRestCobranzaService.getMontosCategoriaMes(dataRequest).subscribe(
      (success) => {
        if (!!success) this.chart_1 = this.generateChart_1(success?.body);
      },
      (error) => {}
    );
  }

  getMontosCategoriaAnio() {
    const dataRequest = {
      anio: this.chart_2_anio ? this.chart_2_anio : '2019',
      catAdeudo: this.chart_2_cat ? this.chart_2_cat.claveSIM : '',
    };
    this.apiRestCobranzaService.getMontoCategoriaAnio(dataRequest).subscribe(
      (success) => {
        if (!!success) {
          this.chart_2 = this.generateChart_2a(success?.body);
          this.chart_2b = this.generateChart_2b(success?.body);
        }
      },
      (error) => {}
    );
  }

  /** ########## FORMATTING ########## */
  generateChart_1(data): any {
    const indexMounts = {
      1: 'Enero',
      2: 'Febrero',
      3: 'Marzo',
      4: 'Abril',
      5: 'Mayo',
      6: 'Junio',
      7: 'Julio',
      8: 'Agosto',
      9: 'Septiembre',
      10: 'Octubre',
      11: 'Noviembre',
      12: 'Diciembre',
    };
    const items = _.map(indexMounts, (_value, _key) => {
      const objFinal = {
        name: _value,
        series: _.map(
          _.filter(data, (_i) => _i.mes == _key),
          (_item) => {
            return {
              name:
                _item?.categoriaAdeudo /**this.indexCategories[_item?.categoriaAdeudo] */,
              value: _item?.totalMontoVencido,
              noAdeudos: _item?.totalCantidadAdeudos,
            };
          }
        ),
      };

      return objFinal;
    });

    console.log('::::: CHART_1: ', JSON.stringify(items));
    return items;
  }

  generateChart_2a(data): any {
    const items = _.map(data, (_item, index) => {
      return {
        name: _item?.categoriaAdeudo, //this.indexCategories[_item?.categoriaAdeudo],
        value: _item?.totalMontoVencido,
      };
    });
    console.log('::::: CHART_2A: ', JSON.stringify(items));
    return items;
  }

  generateChart_2b(data): any {
    const totalAdeudos = data.reduce((acc, _ie) => {
      return acc + _ie.totalCantidadAdeudos;
    }, 0);
    const items = _.map(data, (_item, index) => {
      return {
        name: _item?.categoriaAdeudo, //this.indexCategories[_item?.categoriaAdeudo],
        value: _item?.totalCantidadAdeudos,
        porcent: this.generatePorcent(
          totalAdeudos,
          _item?.totalCantidadAdeudos
        ),
        color: this.colors[index],
      };
    });
    const objFinal = { total: totalAdeudos, data: items };

    console.log('::::: CHART_2B: ', JSON.stringify(objFinal));
    return objFinal;
  }

  generatePorcent(total, monto): any {
    return total && monto ? `${Math.round((monto * 100) / total)}%` : '0%';
  }
}
