import { Component, OnInit } from '@angular/core';

import * as _ from 'lodash';
// SERVICES
import { ApiRestCobranzaService } from '../services/api-rest-cobranza.service';
import { ApiRestSharedService } from '../../shared/services/api-rest-shared.service';

@Component({
  selector: 'app-notificaciones',
  templateUrl: './notificaciones.component.html',
  styleUrls: ['./notificaciones.component.scss'],
})
export class NotificacionesComponent implements OnInit {
  chart_1: any = {
    data: [],
    sizeView: undefined,
    showXAxis: false,
    showYAxis: true,
    gradient: true,
    showLegend: true,
    legendTitle: 'Medios Entrega',
    showXAxisLabel: false,
    xAxisLabel: '',
    showYAxisLabel: true,
    yAxisLabel: 'Montos de Cartas Emitidas',
    colorScheme: {
      domain: ['#165BF6', '#1381D4', '#21C8EB', '#13D4C0', '#16F69F'],
    },
  };
  // NGMODEL
  chart_1_anio: string = '2019';
  chart_1_mesDel: any;
  chart_1_mesAl: any;
  chart_1_cat: any;
  chart_1_col: any;
  chart_1_estatus: any;
  chart_1_monto: any;
  // CATALOGOS
  catalogos = {
    ANIOS: {
      apiRest: false,
      data: ['2020', '2019', '2018', '2017', '2016', '2015'],
    },
    MESES: {
      apiRest: false,
      data: [
        { clave: 1, nombre: 'Enero' },
        { clave: 2, nombre: 'Febrero' },
        { clave: 3, nombre: 'Marzo' },
        { clave: 4, nombre: 'Abril' },
        { clave: 5, nombre: 'Mayo' },
        { clave: 6, nombre: 'Junio' },
        { clave: 7, nombre: 'Julio' },
        { clave: 8, nombre: 'Agosto' },
        { clave: 9, nombre: 'Septiembre' },
        { clave: 10, nombre: 'Octubre' },
        { clave: 11, nombre: 'Noviembre' },
        { clave: 12, nombre: 'Diciembre' },
      ],
    },
    COLONIA: { apiRest: true, data: [] },
    ESTATUS_NOTIFICACION: { apiRest: true, data: [] },
    MONTOS_ADEUDOS: {
      apiRest: false,
      data: [
        '10,000 - 20,000',
        '20,000 - 40,000',
        '50,000 - 70,000',
        '70,000 -',
      ],
    },
    CAT_ADEUDO: { apiRest: true, data: [] },
  };
  constructor(
    private apiRestCobranzaService: ApiRestCobranzaService,
    private apiRestSharedService: ApiRestSharedService
  ) {
    _.each(this.catalogos, (v, k) => {
      if (v?.apiRest) this.getCatalogo(k);
    });
  }

  ngOnInit(): void {
    this.getMedioEntregaNotificacion();
  }

  chart_1_selectFilter(eve, filter) {
    console.log('::::: CHART_1_FILTER: ', filter, eve);
    if (!eve && filter == 'anio') this.chart_1_anio = '2019';

    if (
      (this.chart_1_mesDel &&
        this.chart_1_mesAl &&
        this.chart_1_mesDel?.clave <= this.chart_1_mesAl?.clave) ||
      (!this.chart_1_mesDel && !this.chart_1_mesAl)
    )
      this.getMedioEntregaNotificacion();
  }

  /** ########## SERVICES ########## */

  getCatalogo(flag_cat) {
    this.apiRestSharedService.getCatalogo(flag_cat).subscribe(
      (success) => {
        if (!!success) this.catalogos[flag_cat].data = success.body;
      },
      (error) => {}
    );
  }

  getMedioEntregaNotificacion() {
    const dataRequest = {
      anio: this.chart_1_anio ? this.chart_1_anio : '2019',
      categoriaAdeudo: this.chart_1_cat ? this.chart_1_cat.claveSIM : '',
      mesInicio: this.chart_1_mesDel ? this.chart_1_mesDel.clave : '',
      mesFin: this.chart_1_mesAl ? this.chart_1_mesAl.clave : '',
      colonia: this.chart_1_col ? this.chart_1_col.claveSIM : '',
      estatusNotificacion: this.chart_1_estatus
        ? this.chart_1_estatus.claveSIM
        : '',
      //   monto: this.chart_1_monto,
    };
    this.apiRestCobranzaService
      .getMedioEntregaNotificacion(dataRequest)
      .subscribe(
        (success) => {
          if (!!success)
            this.chart_1.data = this.generateChart_1(success?.body);
        },
        (error) => {}
      );
  }

  /** ########## FORMATTING ########## */
  generateChart_1(data): any {
    const items = _.map(data, (_item, index) => {
      return {
        name: _item?.medioNotificacion,
        value: _item?.totalMontoAdeudoNotificado,
        noNot: _item?.cantidadNotificaciones,
      };
    });
    console.log('::::: CHART_1: ', JSON.stringify(items));
    return items;
  }
}
