import { Component, OnInit } from '@angular/core';
import { ApiEjemploService } from './ApiEjemploService.service';

@Component({
  selector: 'app-pruebajvc',
  templateUrl: './pruebajvc.component.html',
  styles: [],
})
export class PruebajvcComponent implements OnInit {
  constructor(private api: ApiEjemploService) {}

  NotariasList: any = [];

  ngOnInit(): void {
    this.getNotarias();
  }

  getNotarias() {
    this.api.getNotarias().subscribe(
      (data) => {
        //console.log('--->>>>' + JSON.stringify(data));
        this.NotariasList = data.body;
        console.log('--->>>>' + JSON.stringify(this.NotariasList));
      },
      (err) => {}
    );
  }
}
